import React, { useState, useEffect } from "react"
import { useDispatch } from "react-redux"
import { IconButton, Box, Typography, Button, Tabs, Tab, useMediaQuery } from "@mui/material"
import { NavigateBefore, NavigateNext } from "@mui/icons-material"
import FavoriteBorderOutlinedIcon  from "@mui/icons-material/FavoriteBorderOutlined"
import AddIcon from "@mui/icons-material/Add"
import RemoveIcon from "@mui/icons-material/Remove"
import { shades } from "../../theme"
import { addToCart } from "../../state"
import { useNavigate , useParams } from "react-router-dom"
import Item from "../../components/Item"

const ItemDetails = () => {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const { itemId } = useParams()
  const [value, setValue] = useState("description")
  const [count, setCount] = useState(1)
  const [item, setItem] = useState(null)
  const [items, setItems] = useState([])
  const matches = useMediaQuery("(min-width:600px)")
  const handleChange = (_, newValue) => setValue(newValue)

  async function getItem(){
    const item = await fetch(`http://localhost:1337/api/items/${itemId}?populate=image`, {method: "GET"})
    const itemJSON = await item.json()
    setItem(itemJSON.data)
  }

  //** gets related items
  async function getItems(){
    const items = await fetch(`http://localhost:1337/api/items?populate=image`, {method: "GET"})
    const itemsJSON = await items.json()
    setItems(itemsJSON.data)
  }

  useEffect(()=>{
    getItem()
    getItems()
  }, [itemId]) 

  

  return (
    <Box width="80%" m="80px auto">
      <Box display="flex" flexWrap="wrap" columnGap="40px">
        {/* IMAGES */}
        <Box flex="1 1 40%" mb="40px">
          <img src={`http://localhost:1337${item?.attributes?.image?.data?.attributes?.formats?.medium?.url}`} 
          alt={item?.name} width="100%" height="100%" style={{objectFit:"contain"}}/>
        </Box>

        {/* ACTIONS */}
        <Box flex="1 1 50%" mb="40px">
          <Box display="flex" justifyContent={matches ? "end" : "center" } alignItems="center">
            {itemId > 1 && 
            <IconButton onClick={()=> navigate(`/item/${Number(itemId) - 1}`)}>
              <NavigateBefore sx={{fontSize:"40px"}}/>
            </IconButton>}
            {itemId < 18 && 
            <IconButton onClick={()=> navigate(`/item/${Number(itemId) + 1}`)}>
              <NavigateNext sx={{fontSize:"40px"}}/>
            </IconButton>}
          </Box>

          <Box m="65px 0 25px 0">
            <Typography variant="h3">{item?.attributes?.name}</Typography>
            <Typography sx={{mt:"5px"}}>${item?.attributes?.price}</Typography>
            <Typography sx={{mt:"20px"}}> {item?.attributes?.longDescription}</Typography>
          </Box>

          {/* COUNT & BUTTONS */}
          <Box display="flex" alignItems="center" minHeight="50px">
            <Box display="flex" alignItems="center" border={`1.5px solid${shades.neutral[300]}`} 
            mr="20px" p="2px 5px">
              
              <IconButton onClick={()=> setCount(Math.max(count -1, 1))}>
                <RemoveIcon/>
              </IconButton>
              <Typography sx={{p: "0 5px"}}>{count}</Typography>
              <IconButton onClick={()=> setCount(count + 1)}>
                <AddIcon/>
              </IconButton>
            </Box>

            <Button sx={{backgroundColor: "#222222", color:"white", borderRadius:0, minWidth:"150px", padding: "10px 40px"}}
            onClick={()=> dispatch(addToCart({item: {...item, count}}))}>
              ADD TO CART
            </Button>
          </Box>

          <Box>
            <Box m="20px 0 5px 0" display="flex">
              <FavoriteBorderOutlinedIcon/>
              <Typography sx={{ml: "5px"}}>ADD TO WISHLIST</Typography>
            </Box>
            <Typography>CATEGORY: {item?.attributes?.category.replace(/([A-Z])/g, " $1").replace(/^./, (str)=> str.toUpperCase())}</Typography>
          </Box>
        </Box>
      </Box>

      {/* INFORMATION */}
      <Box m="20px 0">
        <Tabs value={value} onChange={handleChange}>
          <Tab label="DESCRIPTION" value="description"/>
          <Tab label="REVIEWS" value="reviews"/>
        </Tabs>
      </Box>
      <Box display="flex" flexWrap="wrap" gap="15px">
        {value === "description" && (
          <div>{item?.attributes?.longDescription}</div>
        )}
        {value === "reviews" && <div>N/A</div>}
      </Box>
      {/* RELATED ITEMS */}
      <Box mt="50px" width="100%">
        <Typography variant="h3" fontWeight="bold"> Related Products </Typography>
      </Box>
      <Box mt="20px" display="flex" flexWrap="wrap" columnGap="1.33%" justifyContent="space-between">
        {items.slice(0,4).map((item, i)=>(
          <Item key={`${item.name}-${i}`} item={item}/>
        ))}
      </Box>
    </Box>
  )
}

export default ItemDetails